// styling boost popup
.boost-popup {
  @extend .popup-content;
  @include radius(12px);
  max-width: 466px;
  max-height: 665px;
  padding: 40px 30px 36px;
  h2 {
    font-weight: $wt600;
    color: $color-grey-dark;
    margin-bottom: 28px;
  }
  p {
    color: $color-grey-dark;
    padding-right: 15px;
    margin-bottom: 41px;
  }
  .input-field {
    color: $color-grey-dark;
    padding: 0 40px 0 30px;
  }
  
//   select, option {
//     word-spacing:178px;
// }
  .form-group {
    margin-bottom: 30px;
  }
  .select {
    @include abs(18px, 32px, auto, auto);
  }
  ::-webkit-input-placeholder {
    color: $color-grey-dark;
  }
  ::-moz-placeholder {
    color: $color-grey-dark;
  }
  :-ms-input-placeholder {
    color: $color-grey-dark;
  }
  :-moz-placeholder {
    color: $color-grey-dark;
  }
  &:after {
    display: none;
  }
}
.pay-wrap {
  border-top: 1px solid $color-light;
  padding: 35px 0 20px;
  margin-top: -30px;
}
.pay-label {
  span {
    color: $color-grey-dark;
    letter-spacing: 0.35px;
  }
  img {
    width: 98px;
  }
}
.icon-clock {
  @include abs(18px, 28px, auto, auto);
  width: 18px;
  height: 18px;
  path {
    fill: $color-blue;
  }
}
.icon-cal {
  @include abs(20px, 30px, auto, auto);
}
// time picker style
.time-picker {
  z-index: 2;
  .label {
    z-index: 1;
  }
  .icon-clock {
    pointer-events: none;
  }
  .react-time-picker {
    width: 100%;
  }
  .react-time-picker__wrapper {
    @include radius(50px);
    @include border(1px, solid, $color-light);
    padding: 0 30px;
    height: 56px;
    font-size: $base-size * 1.8;
    input {
      -webkit-appearance: none;
      &:focus {
        outline: none;
      }
    }
  }
  .react-time-picker__inputGroup{
    display: flex;
    align-items: center;
    .react-time-picker__inputGroup__leadingZero{
      color: $color-grey-dark;
    }
  }
  .react-time-picker__inputGroup__input {
    color: $color-grey-dark;
  }
  .react-time-picker__inputGroup__amPm {
    display: none;
  }
  .react-time-picker__button {
    display: none;
  }
}
// date picker css
.date-picker {
  .label {
    z-index: 1;
  }
  .icon-cal {
    pointer-events: none;
  }
  .react-datepicker-wrapper {
    width: 100%;
    input {
      @include radius(50px);
      @include border(1px, solid, $color-light);
      color: $color-grey-dark;
      padding: 0 30px;
      height: 56px;
      width: 100%;
      font-size: $base-size * 1.8;
      -webkit-appearance: none;
      &:focus {
        outline: none;
      }
    }
  }
  
  .react-datepicker-popper {
    z-index: 2;
  }
}
@media all and (max-width: 767px) {
  .boost-popup {
    padding: 30px 15px;
    .form-group {
      margin-bottom: 30px;
    }
    .select {
      top: 22px;
    }
  }
  .pay-wrap {
    margin-top: 0;
  }
  .time-picker {
    .react-time-picker__wrapper {
      font-size: $base-size * 1.4;
    }
  }
  .date-picker {
    .react-datepicker-wrapper {
      input {
        font-size: $base-size * 1.4;
      }
    }
  }
}

// custom-selectbox
.select-wrap{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding-right: 30px;
  
}
.select-wrap:after{
  content:"";
  position: absolute;
  height: 10px;
  width: 10px;
  border-style: solid;
  border-color: #000;
  border-width: 0px 1px 1px 0px;
  transform: rotate(45deg);
  transition: border-width 150ms ease-in-out;
  right: 15px;
  top: 19px;
}
.select-list-wrap{
  padding: 0 30px;
  position: absolute;
  top: 100%;
  width: 100%;
  background-color: #fff;
  z-index: 5;
}
.select-list{
  border: 1px solid #d8d6d6;
}
.select-list li{
  border: 1px solid #d8d6d6;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  color: #5a5a5a;
  padding: 10px 40px 10px 30px;
  background: #FAFAFA;
}
.select-list li:hover{
  // border: 1px solid #5867b0;
  color: #5867b0;
}
.select-act{
  display: none;
}